.edit {
    max-width: 847px;
    width: 100%;
    grid-gap: 40px;
    padding-top: 45px;
  
    &__modal-content {
      padding: 0 30px;
    }
  
    &__select {
      width: 100%;
      height: 44px;
      border: 2px solid var(--light-green);
      border-radius: 40px;
    }
  
    &__select-header {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 100%;
      padding: 0 15px 0 45px;
      cursor: pointer;
  
      svg {
        margin-left: 18px;
        transform: rotate(-90deg);
        fill: var(--green);
      }
  
      &::before {
        position: absolute;
        content: '';
        background-image: var(--table);
        background-position: center;
        background-repeat: no-repeat;
        width: 24px;
        height: 24px;
        top: 50%;
        left: 14px;
        transform: translateY(-50%);
        z-index: 5;
      }
    }
  
    &__select-current {
      min-width: 27px;
    }
  
    &__select-body {
      display: none;
      flex-direction: column;
      grid-gap: 3px;
      margin-top: 10px;
      width: 100%;
      padding: 10px 0;
      background-color: var(--white);
      border-radius: 12px;
      box-shadow: 0px 4px 15px rgba(212, 212, 212, 0.64);
    }
  
    &__select-item {
      height: 25px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
  
      &:hover {
        background-color: rgba(100, 182, 172, 0.3);
        color: var(--green);
      }
    }

    .modal__title {
      text-align: left;
      padding-left: 60px;
    }

    .pagination {
      order: unset;
    }
}