.content {
    flex: 1 1 auto;
    display: flex;

    @media (max-width: 480px) {
        flex-direction: column;
    }

    &__body {
        padding: 48px 25px;
        flex: 1 1 auto;
        min-width: 0;

        @media (max-width: 768px) {
            padding: 18px 25px;
        }

        @media (max-width: 480px) {
            padding: 8px 20px;
        }
    }

    &__title-mobile {
        display: none;
        padding: 13px 18px 7px;

        @media (max-width: 480px) {
            display: block;
            font-family: var(--font-family-futura);
            font-weight: 700;
            font-size: 20px;
            line-height: 25px;
        }
    }

    &__subtitle {
        margin-top: 40px;
        font-family: var(--font-family-futura);
        font-weight: 700;
        font-size: 20px;
        line-height: 25px;

        @media (max-width: 480px) {
            font-weight: 600;
            font-size: 18px;
            margin-top: 32px;
        }
    }

    &__table {
        padding: 0;
        margin-top: 20px;

        .table__product-title {
            @media (min-width: 1920px) {
                flex-grow: 0;
            }
        }

        .table__product-item {
            border-radius: 20px;
        }

        .table__product-item > div
        {
            text-align: center;
        }

        .table__product-item > button {
            display: flex;
            justify-content: center;
        }
    }

    &__btn {

        &-right {
            display: flex;
            justify-content: flex-end;
        }

        &-add {
            margin-top: 15px;
            padding: 11px 22px;
            display: flex;
            align-items: center;

            svg {
                width: 24px;
                height: 24px;
                margin-right: 10px;
            }
        }        
    }
}

.header-content {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;

    &__title {
        font-family: var(--font-family-futura);
        font-weight: 700;
        font-size: 30px;
        line-height: 38px;
        color: #143127;
        margin-right: 15px;
        padding-bottom: 10px;


        @media (max-width: 768px) {
            flex: 0 0 100%;
            font-size: 24px;
            margin-bottom: 13px;
        }

        @media (max-width: 480px) {
            font-size: 16px;
            margin-bottom: 8px;
        }
    }    

    &__btns {
        padding-left: 15px;
        grid-gap: 10px;
        display: flex;

        @media (max-width: 993px) {
            padding-left: 0;
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
            width: 24px;
            height: 24px;
            margin-right: 10px;
        }

        @media (max-width: 993px) {
            padding: 10px 15px;
            font-size: 14px;
        }
    }
}

