.content__table-warehouses {

    .table__wrapp {

        &::-webkit-scrollbar {
            transform: translate(0, -50px);
            height: 100px;
          }

          &::-webkit-scrollbar-thumb {
            background-color: transparent;
            transform: translate(0, -100px);
          }
    }
    
    .table__product {
        min-width: 1300px;
    }

    .table__product-amount {
        width: 27px;
        margin-right: 15px;
    }

    .table__product-title {
        width: 200px;
        margin-right: 15px;
    }

    .table__product-whois {
        width: 120px;
        margin-right: 15px;
    }

    .table__product-source {
        width: 140px;
        margin-right: 15px;
    }

    .table__product-count {
        width: 110px;
        margin-right: 15px;
    }

    .table__product-added {
        width: 80px;
        margin-right: 15px;
    }

    .table__product-priority {
        width: 80px;
        margin-right: 15px;
    }

    .table__product-status {
        width: 55px;
        margin-right: 15px;
    }

    .table__product-autorefresh {
        width: 170px;
        margin-right: 15px;
    }

    .table__product-actions {
        width: 170px;
        margin-right: 15px;
    }

    .table__product-item {

        &:last-child {

            .autorefresh__select-body {
                bottom: 32px;
            }
        }

        &:nth-last-child(2) {
            .autorefresh__select-body {
                top: 18px;
            }
        }

        &:not(:first-child) {

            .table__product-actions {
                justify-content: flex-end;
            }            
        }
    }
}

.content__table-campare {
    margin-top: 0;

    .table__product {
        min-width: 727px;
        padding: 0 60px;
    }

    .table__product-item {

        &:last-child {

            .table__product-titlefull {
                bottom: 20px;
            }            
        }
    }

    .table__product-title {
        width: 370px;
        margin-right: 15px;
    }

    .table__product-partner-art {
        width: 140px;
        margin-right: 15px;
    }

    .table__product-art {
        width: 140px;
        margin-right: 15px;
    }
}

.table__product-switcher {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 54px;
    height: 30px;    
    box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
    border-radius: 20px;
    font-family: var(--font-family-futura);
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: #626F6A;

    &.on {  
        background: #CCEFC4;
    }

    &.off {
        background: #FBCACA;
    }
}

.autorefresh {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &__info-block {
    }

    &__input-block {
    }

    &__input {
        width: 60px;
        height: 30px;
        border: 2px solid #BDDEDA;
        border-radius: 40px;        
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #626F6A;
        text-align: center;
    }

    &__info-block {
    }

    &__select {
        width: 105px;
        height: 30px;
        border: 2px solid var(--light-green);
        border-radius: 40px;

        &--time {
            width: 105px;
            margin-left: 10px;

            @media (max-width: 993px) {
                margin-left: 6px;
            }

            @media (max-width: 540px) {
                transform: translate(-87%, 0);
            }
  
            &::before {
              position: absolute;
              content: '';
              mask: url("../img/sprite/clock-min.svg");
              mask-position: center;
              mask-repeat: no-repeat;
              background: #BDDEDA;
              width: 15px;
              height: 15px;
              top: 50%;
              left: 9px;
              transform: translateY(-50%);
              z-index: 5;
            }
          }
    }

    &__select-header {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;
        padding: 0 15px 0 37px;
        cursor: pointer;        
    
        svg {
          margin-left: 18px;
          transform: rotate(-90deg);
          fill: var(--green);
        }
    }

    &__select-current {
        min-width: 27px;
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #626F6A;

        @media (max-width: 768px) {
            font-size: 16px;
        }
    }

    &__select-body {
        display: none;
        flex-direction: column;
        grid-gap: 3px;
        margin-top: 10px;
        width: 100%;
        padding: 10px 0;
        background-color: var(--white);
        border-radius: 12px;
        box-shadow: 0px 4px 15px rgba(212, 212, 212, 0.64);
    }

    &__select-item {
        height: 25px;
        display: flex;
        align-items: center;
        //justify-content: center;
        cursor: pointer;
        padding: 0 20px;
    
        &:hover {
          background-color: rgba(100, 182, 172, 0.3);
          color: var(--green);
        }
    }

    &__unavailable {
        width: 170px;
        height: 30px;
        background: #FBCACA;
        box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
        border-radius: 20px;
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #626F6A;
    }
}

.available {
    .on {
        display: block;
    }

    .off {
        display: none;
    }

    .autorefresh__unavailable {
        display: none;
    }

    .autorefresh__info-block {
        display: block;
    }
}

.unavailable {
    .on {
        display: none;
    }

    .off {
        display: block;
    }

    .autorefresh__unavailable {
        display: flex;
    }

    .autorefresh__info-block {
        display: none;
    }
}