.form {
  &__info {
    width: 100%;
    display: flex;
    align-items: flex-end;
    grid-gap: 12px;

    @media (max-width: 768px) {
      flex-wrap: wrap;
    }
  }

  &__info-block {
    display: flex;
    flex-direction: column;
    grid-gap: 7px;
    max-width: 376px;
    width: 100%;

    &--search {
      @media (max-width: 576px) {
        max-width: 100%;
      }
    }

    &--categories {
      @media (max-width: 768px) {
        order: 1;
        max-width: 100%;
      }

      @media (max-width: 576px) {
        order: 1;
        max-width: 100%;
      }
    }

    &--sorting {
      width: initial;

      @media (max-width: 576px) {
        order: 1;
      }
    }
  }

  &__label {
    position: relative;
    display: block;
    height: 44px;
    width: 100%;
    display: flex;
    align-items: center;
    grid-gap: 7px;

    &--search {
      justify-content: flex-end;
      svg {
        width: 44px;
        height: 44px;
      }

      &::before {
        position: absolute;
        content: '';
        background-image: var(--search-icon);
        background-position: center;
        background-repeat: no-repeat;
        width: 24px;
        height: 24px;
        top: 50%;
        left: 14px;
        transform: translateY(-50%);
        z-index: 5;
      }
    }
  }

  &__input {
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    padding-left: 45px;
    padding-right: 25px;
    border-radius: 40px;
    border: 2px solid var(--light-green);
    transition: all 0.3s ease;
    z-index: 1;

    &:focus,
    &:active {
      border: 2px solid var(--green);
    }

    &::placeholder {
      opacity: 0.5;
    }
  }

  &__search-btn {
    display: none;
    height: 44px;
    right: 0;
    z-index: 10;

    @media (max-width: 576px) {
      display: flex;
    }
  }

  &__select {
    width: 124px;
    height: 44px;
    border: 2px solid var(--light-green);
    border-radius: 40px;

    &--catalog {
      width: 450px;
    }
  }

  &__select-header {
    position: relative;
    display: flex;
    align-items: center;
    height: 100%;
    padding-left: 45px;
    cursor: pointer;

    svg {
      margin-left: 18px;
      transform: rotate(-90deg);
      fill: var(--green);
    }

    &::before {
      position: absolute;
      content: '';
      background-image: var(--amount);
      background-position: center;
      background-repeat: no-repeat;
      width: 24px;
      height: 24px;
      top: 50%;
      left: 14px;
      transform: translateY(-50%);
      z-index: 5;
    }
  }

  &__select-current {
    min-width: 27px;
  }

  &__select-body {
    display: none;
    flex-direction: column;
    grid-gap: 3px;
    margin-top: 10px;
    width: 118px;
    padding: 10px 0;
    background-color: var(--white);
    border-radius: 12px;
    box-shadow: 0px 4px 15px rgba(212, 212, 212, 0.64);
  }

  &__select-item {
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    &:hover {
      background-color: rgba(100, 182, 172, 0.3);
      color: var(--green);
    }
  }

  &__categories {
    position: relative;
    height: 44px;
    display: flex;
    align-items: center;
    grid-gap: 7px;
    padding-left: 14px;
    padding-right: 7px;
    border: 2px solid #ACDED8;
    border-radius: 40px;

    svg {
      width: 24px;
    }
  }

  &__categories-filter {
    display: flex;
    cursor: pointer;
  }

  &__categories-blur {
    position: absolute;
    right: 0;
    height: 40px;
    width: 82px;
    background: linear-gradient(270deg, #FFFFFF 2.93%, rgba(255, 255, 255, 0) 100%);
    border-radius: 0px 40px 40px 0px;
    pointer-events: none;
  }

  &__categories-content {
    display: flex;
    align-items: center;
    grid-gap: 6px;
    overflow-x: scroll;
    cursor: pointer;
    margin-bottom: -5px;

    &::-webkit-scrollbar {
      height: 5px;
    }

    &::-webkit-scrollbar-track {
      height: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: transparent;
    }
  }

  &__categories-text {
    color: rgba(20, 49, 39, 0.5);
  }

  &__btns {
    display: flex;
    align-items: flex-end;
    grid-gap: 14px;
    height: 70px;
  }

  input[type="search"]::-webkit-search-cancel-button {
    -webkit-appearance: none;
    height: 1em;
    width: 1em;
    border-radius: 50em;
    background: var(--input-search-cancel) no-repeat 50% 50%;
    background-size: contain;
    opacity: 0;
    pointer-events: none;
  }

  input[type="search"]:focus::-webkit-search-cancel-button {
    opacity: 1;
    pointer-events: all;
  }
}

.form-modal {
  display: flex;
  flex-direction: column;
  padding: 10px 60px 18px;
  background: var(--white);
  border-radius: 20px;
  box-shadow: none;
  grid-gap: 20px;

  @media (max-width: 1240px) {
    flex-wrap: wrap;
  }

  @media (max-width: 768px) {
      padding: 27px 26px 27px 20px;
  }

  @media (max-width: 480px) {
      padding: 11px 13px;
  }

  .form__title {
      font-size: 16px;
      text-align: left;
  }

  .form__btns {
      @media (max-width: 480px) {
          height: unset;
      }
  }

  &__info {
      display: flex;
      align-items: flex-end;
      grid-gap: 40px;
      padding-right: 10px;
      flex: 1 1 auto;

      @media (max-width: 1600px) {
          grid-gap: 10px;
      }

      @media (max-width: 1250px) {
          flex-wrap: wrap;
          grid-gap: 24px;
      }

      @media (max-width: 480px) {
          grid-gap: 15px;
      }
  }

  &__form {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
  }

  &__info-block {
      display: flex;
      flex-direction: column;
      grid-gap: 7px;

      &:first-child {
          flex: 0 1 450px;

          @media (max-width: 1250px) {
              flex: 0 0 100%;
          }
      }

      &:nth-child(2) {

          @media (max-width: 1250px) {
              flex: 0 0 100%;
          }
      }

      &:nth-child(4) {

          @media (max-width: 480px) {
              margin: -40px 0 0 -73px;
              transform: translate(145px, -20px);
          }
      }
  }

  &__select {
      width: 124px;
      height: 44px;
      border: 2px solid var(--light-green);
      border-radius: 40px;
  
      &--catalog {
        width: 100%;
        min-width: 200px;

        &::before {
          position: absolute;
          content: '';
          mask: url("../img/sprite/folder-open.svg");
          mask-position: center;
          mask-repeat: no-repeat;
          background: #BDDEDA;
          width: 24px;
          height: 24px;
          top: 50%;
          left: 14px;
          transform: translateY(-50%);
          z-index: 5;
        }
      }

      &--time {
          width: 132px;
          margin-left: -25px;

          @media (max-width: 1600px) {
              margin-left: 0;
          }

          &::before {
            position: absolute;
            content: '';
            mask: url("../img/sprite/clock.svg");
            mask-position: center;
            mask-repeat: no-repeat;
            background: #BDDEDA;
            width: 24px;
            height: 24px;
            top: 50%;
            left: 14px;
            transform: translateY(-50%);
            z-index: 5;
          }
        }
  }

  &__select-header {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 100%;
      padding: 0 15px 0 45px;
      cursor: pointer;
      font-family: var(--font-family-futura);
  
      svg {
        margin-left: 18px;
        transform: rotate(-90deg);
        fill: var(--green);
      }
  }

  &__select-current {
      min-width: 27px;

      @media (max-width: 768px) {
          font-size: 16px;
      }
  }

  &__select-body {
      display: none;
      flex-direction: column;
      grid-gap: 3px;
      margin-top: 10px;
      width: 100%;
      padding: 10px 0;
      background-color: var(--white);
      border-radius: 12px;
      box-shadow: 0px 4px 15px rgba(212, 212, 212, 0.64);
  }

  &__select-item {
      height: 25px;
      display: flex;
      align-items: center;
      padding: 0 20px;
      cursor: pointer;
  
      &:hover {
        background-color: rgba(100, 182, 172, 0.3);
        color: var(--green);
      }
  }

  &__checkbox-label {
      height: 100%;
      position: relative;
      display: flex;
      align-items: center;
      margin-bottom: 12px;

      @media (max-width: 1250px) {
          margin-bottom: 0;
      }
  }

  &__checkbox-input {
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0;
      transition: all 0.3s ease;

      &:checked ~ .filter__checkbox-name::after{
      opacity: 1;
      }

      &:checked ~ .filter__checkbox-name::before{
      background-color: var(--white);
      border: none;
      }
  }

  &__checkbox-name {
      position: relative;
      padding-left: 34px;
      font-size: 18px;
      color: var(--gray);
      transition: all 0.3s ease;
      cursor: pointer;
      white-space: nowrap;

      &::before {
      content: "";
      position: absolute;
      display: block;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background-color: var(--white);
      width: 24px;
      height: 24px;
      border: 2px solid var(--light-green);
      border-radius: 6px;
      transition: all .3s ease;
      z-index: 1;
      }

      &::after {
      content: "";
      position: absolute;
      display: block;
      background-image: var(--arrow-check);
      background-size: cover;
      background-repeat: no-repeat;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 24px;
      height: 24px;
      z-index: 5;
      opacity: 0;
      transition: all 0.3s ease;
      }

      &:hover {
          transition: all 0.3s ease;

          &::before {
              background-color: var(--light-green);
          }
      }

      @media (max-width: 768px) {
          font-size: 16px;
      }
  }

  &__input-block {
      display: flex;
      flex-direction: column;
      align-items: center;
      column-gap: 14px;
      font-family: var(--font-family-futura);
      font-weight: 400;
      font-size: 18px;
      line-height: 19px;
      grid-gap: 8px;

      @media (max-width: 768px) {
          font-size: 16px;
      }

      @media (max-width: 480px) {
          flex-wrap: wrap;
      }
  }

  &__input-name {      
      white-space: nowrap;
      align-self: flex-start;
      font-family: var(--font-family-futura);
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #143127;

      @media (max-width: 480px) {
          flex: 0 0 100%;
      }
  }

  &__input-wrapper {
    position: relative;
    width: 100%;

    &::before {
      position: absolute;
      content: '';
      mask: url("../img/sprite/folder-open.svg");
      mask-position: center;
      mask-repeat: no-repeat;
      background: #BDDEDA;
      width: 24px;
      height: 24px;
      top: 50%;
      left: 14px;
      transform: translateY(-50%);
      z-index: 5;
    }
  }

  &__input {
      height: 44px;
      border: 2px solid var(--light-green);
      border-radius: 40px;
      padding: 3px 10px 0 24px;
      font-family: var(--font-family-futura);
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #143127;

      @media (max-width: 768px) {
          font-size: 16px;
      }
  }

  &__catalog {
    display: flex;
    width: 100%;
    grid-gap: 40px;

    .form-modal__input {
      padding-left: 45px;
    }

    .form-modal__info-block {
      &:first-child {
        flex: 1 1 auto;
      }
      &:last-child {
        flex: 0 0 270px;

        @media (max-width: 768px) {
          flex: 1 1 auto;
        }
      }
    }

    @media (max-width: 768px) {
      flex-direction: column;
      grid-gap: 15px;
    }
  }

  &__link {
    margin-top: 5px;    

    .form-modal__input {
      padding-left: 45px;
    }
  }

  &__tags {
    margin-top: 5px;

    .form-modal__info-block {
      flex-direction: row;
      grid-column-gap: 47px;
      grid-row-gap: 17px;
      flex-wrap: wrap;
      @media (max-width: 480px) {
        flex-direction: column;
      }
    }

    .form-modal__input-block {
      flex: 1 1 45%;

      @media (max-width: 480px) {
        flex: 1 1 auto;
      }
    }
  }

  &__extra {

  }

  &__info-wrapper {
    display: flex;
    flex-wrap: wrap;
    grid-column-gap: 22px;
    grid-row-gap: 17px;

    .form-modal__input-block {
      @media (max-width: 480px) {
        flex-direction: row;
        //flex-wrap: nowrap;
      }
    }

    .form-modal__input-name {
      text-align: left;
    }

    .form-modal__info-block {
      flex: 1 1 30%;

      &:nth-child(4) {
        @media (max-width: 480px) {
          margin: 0;
          transform: translate(0, 0);
        }
      }

      @media (max-width: 768px) {
        flex: 1 1 45%;
      }

      @media (max-width: 540px) {
        flex: 1 1 100%;
      }
    }

    .form-modal__select {
      width: unset;
    }

    .form-modal__select-header {
      padding-left: 24px;
    }
  }

  &__subtitle {
    font-family: var(--font-family-futura);
    font-weight: 450;
    font-size: 20px;
    line-height: 25px;
    color: #143127;
    margin-bottom: 21px;

    @media (max-width: 768px) {
      font-size: 18px;
    }
  }

  &__autorefresh {
    display: flex;
    grid-gap: 10px;
    position: relative;
    z-index: 1;

    .form-modal__info-block {

      &:first-child {
        .autorefresh__input-block {
          display: none;
        }
        .autorefresh__info-block {
          display: none;
        }

        &.active {

          .autorefresh__input-block {
            display: flex;
          }
          .autorefresh__info-block {
            display: block;
          }
        }
      }      
    } 

    .autorefresh__select--time {
      transform: translate(0, 0);
    }

    @media (max-width: 540px) {
      flex-direction: column;
    }
    
    .filter__checkbox-label {
      margin-right: 25px;

      @media (max-width: 540px) {
        height: 30px;
      }
    }

    .form-modal__info-block {
      flex-direction: row;
      grid-gap: 0;

      &:first-child {
        @media (max-width: 1250px) {
          flex: 1 1 auto;
        }
      }

      &:nth-child(2) {
        flex: 0 0 270px;

        @media (max-width: 540px) {
          flex: 1 1 auto;
        }
      }
    }
  }
}

.autorefresh__input-block {
  display: flex;
  grid-gap: 15px;

  .form-modal__input-name {
    align-self: center;
    
    @media (max-width: 480px) {
      flex: 1 1 auto;
    }
  }
}